import React from 'react';
import { HtmlProcessingInstruction } from '../HtmlProcessingInstruction';
import { Logger } from '@uniformdev/common';
import { convertAttribs } from '../attribMap';

const { ProcessNodeDefinitions } = require('html-to-react');

const processNodeDefinitions = new ProcessNodeDefinitions(React);

export const processDefaultNode = (node: any, children: any, index: number) => {
    return processNodeDefinitions.processDefaultNode(node, children, index);
};
// Anything else
export const buildDefaultNodeProcessor = (logger: Logger): HtmlProcessingInstruction => ({
    shouldProcessNode: () => true,
    processNode: function (node: any, children: any, index: number) {
        if (node.type === 'text') {
            const data = node.data ? node.data : '';
            if (!data) return null;
            node.data = data;
        }
        convertAttribs(node, logger);
        return processDefaultNode(node, children, index);
    },
});
